<template>
    <div class="index">
        <div class="cont">
            <el-button type="primary" icon="el-icon-refresh" @click="click1">随机人员信息</el-button>
            <ul class="box">
                <li class="flex-row-vcenter">
                    <span>户名</span>
                    <span class="m-l-20">{{ userInfo.custName }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(userInfo.custName)">复制</el-button>
                </li>
                <li class="flex-row-vcenter">
                    <span>户号</span>
                    <span class="m-l-20">{{ userInfo.custNo }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(userInfo.custNo)">复制</el-button>
                </li>
                <li class="flex-row-vcenter">
                    <span>手机</span>
                    <span class="m-l-20">{{ userInfo.phone }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(userInfo.phone)">复制</el-button>
                </li>
                <li class="flex-row-vcenter">
                    <span>地址</span>
                    <span class="m-l-20"> {{ userInfo.address }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(userInfo.address)">复制</el-button>
                </li>
            </ul>
        </div>

        <div class="cont">
            <el-button type="primary" icon="el-icon-refresh" @click="click3">随机台区信息</el-button>
            <ul class="box">
                <li class="flex-row-vcenter">
                    <span>台区标识</span>
                    <span class="m-l-20">{{ tgInfo.tgId }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(tgInfo.tgId)">复制</el-button>
                </li>
                <li class="flex-row-vcenter">
                    <span>台区编码</span>
                    <span class="m-l-20">{{ tgInfo.tgNo }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(tgInfo.tgNo)">复制</el-button>
                </li>
                <li class="flex-row-vcenter">
                    <span>台区名称</span>
                    <span class="m-l-20">{{ tgInfo.tgName }}</span>
                    <el-button class="m-l-20" type="primary" size="mini" @click="copy(tgInfo.tgName)">复制</el-button>
                </li>
            </ul>
        </div>

        <div class="cont">
            <el-button type="primary" icon="el-icon-refresh" @click="click2">随机ID</el-button>
            <el-input-number v-model="num" :min="1" :max="100" label="描述文字" class="m-l-20"></el-input-number>
            <div class="box m-t-10 flex-row-vcenter">
                <span>{{ id }}</span>
                <el-button class="m-l-20" type="primary" size="mini" @click="copy(id)">复制</el-button>
            </div>
        </div>
    </div>
</template>

<script>
import { clipboard } from '@/utils/util';
export default {
    filters: {},
    components: {},
    mixins: [],
    props: {},
    data() {
        return {
            num: 16,
            id: '',
            userInfo: {
                custName: '',
                custNo: '',
                phone: '',
                address: '',
            },
            tgInfo: {
                tgId: '',
                tgNo: '',
                tgName: '',
            },
        };
    },
    computed: {},
    watch: {},
    onLoad() {},
    onUnload() {},
    created() {},
    mounted() {},
    beforeDestroy() {},
    methods: {
        click1() {
            let res = generateRandomPerson();
            console.log(`res`, res);
            this.userInfo.custName = res.custName;
            this.userInfo.custNo = res.custNo;
            this.userInfo.phone = res.phone;
            this.userInfo.address = res.address;
        },
        click2() {
            let res = generateRandomNumberString(this.num);
            this.id = res;
        },
        click3() {
            let res = generateRandomTransformerArea();
            this.tgInfo.tgId = res.tgId;
            this.tgInfo.tgNo = res.tgNo;
            this.tgInfo.tgName = res.tgName;
        },
        /**
         * 复制
         */
        copy(item) {
            if (item) {
                clipboard(item);
                this.$message.success('复制成功');
            }
        },
    },
};
/** 工具函数 */
function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

function randomNumber(length) {
    return Array.from({ length }, () => Math.floor(Math.random() * 10)).join('');
}

/**
 * 生成随机人员信息
 */
function generateRandomPerson() {
    const surnames = ['李', '王', '张', '刘', '陈', '杨', '赵', '黄', '周', '吴'];
    const names = ['伟', '芳', '娜', '敏', '静', '丽', '强', '磊', '军', '洋', '勇', '艳'];

    // 姓名
    const surname = surnames[randomInt(0, surnames.length - 1)];
    const name = names[randomInt(0, names.length - 1)] + (Math.random() > 0.6 ? names[randomInt(0, names.length - 1)] : '');

    const custName = surname + name;
    const custNo = '410' + String(Date.now()).slice(-7) + randomInt(100, 999);

    // 手机号
    const phonePrefixes = [
        '130',
        '131',
        '132',
        '133',
        '135',
        '136',
        '137',
        '138',
        '139',
        '150',
        '151',
        '152',
        '155',
        '156',
        '157',
        '158',
        '159',
        '170',
        '171',
        '173',
        '175',
        '176',
        '177',
        '178',
        '180',
        '181',
        '182',
        '183',
        '185',
        '186',
        '187',
        '188',
        '189',
    ];
    const phone = phonePrefixes[randomInt(0, phonePrefixes.length - 1)] + randomNumber(8);

    // 地址数据池
    const provinces = ['河南省', '河北省', '山东省', '江苏省', '浙江省'];
    const cities = ['郑州市', '石家庄市', '济南市', '南京市', '杭州市'];
    const districts = ['中原区', '新华区', '历下区', '鼓楼区', '西湖区'];
    const streets = ['建设路街道', '人民路街道', '解放路街道', '文化路街道', '朝阳路街道'];
    const communities = ['绿城花园', '阳光小区', '幸福家园', '锦绣华庭', '和谐社区'];

    const address =
        provinces[randomInt(0, provinces.length - 1)] +
        cities[randomInt(0, cities.length - 1)] +
        districts[randomInt(0, districts.length - 1)] +
        streets[randomInt(0, streets.length - 1)] +
        communities[randomInt(0, communities.length - 1)] +
        '小区' +
        randomInt(1, 30) +
        '号楼' +
        randomInt(1, 4) +
        '单元' +
        randomInt(101, 2603) +
        '室';

    return {
        custName,
        custNo,
        address, // 地址
        phone, // 手机号
    };
}

/**
 * 生成指定位数的纯数字随机字符串
 * @param {number} length 位数
 * @returns {string}
 */
function generateRandomNumberString(length) {
    return Array.from({ length }, () => Math.floor(Math.random() * 10)).join('');
}

/**
 * 生成随机台区信息
 */
function generateRandomTransformerArea() {
    // 地名池
    const places = ['雁鸣湖', '龙湖', '白沙', '中牟', '新区', '南关', '北关', '东区', '西区', '滨河', '高新', '开发区', '工业园'];

    // 行政区后缀
    const areaSuffix = ['镇', '街道', '乡'];

    // 变电 / 台区类型
    const stationTypes = ['变电站', '配变', '台变'];

    // 台区名称
    const areaName =
        places[randomInt(0, places.length - 1)] +
        areaSuffix[randomInt(0, areaSuffix.length - 1)] +
        (Math.random() > 0.5 ? places[randomInt(0, places.length - 1)] : '') +
        stationTypes[randomInt(0, stationTypes.length - 1)];

    return {
        tgId: generateRandomNumberString(10), // 台区ID
        tgNo: generateRandomNumberString(10), // 台区编号
        tgName: areaName, // 台区名称
    };
}
</script>

